<template>
  <div>
    <van-row class="row" type="flex" justify="center">
      <van-col span="8"></van-col>
      <van-col
        span="6"
        class="text-center"
        v-for="(item,index) in tabs"
        :key="index"
        @click="tabChange(item)"
      >
        <router-link :to="{name:item.name}" :class="{'active':item.isactive}">{{item.title}}</router-link>
      </van-col>

      <van-col span="8"></van-col>
    </van-row>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tabs: [
        {
          title: "推荐",
          name: "Recommend",
          isactive: true
        },
        {
          title: "菜单",
          name: "Menu",
          isactive: false
        }
      ]
    };
  },
  created() {
    this.inStart();
  },

  methods: {
    tabChange(item) {
      this.tabs.forEach(v => {
        v.isactive = false;
      });
      item.isactive = true;
    },

    // 初始化
    inStart() {
      this.tabs.forEach(v => {
        v.isactive = false;

        if (v.name == this.$route.name) {
          v.isactive = true;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../assets/css/commaint.less";
.van-row {
  background-color: #2c1907;
  color: #fff;
  height: 34px;
  line-height: 26px;
  font-size: 18px;
  padding: 10px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
}

.van-icon {
  font-size: 24px;
  line-height: 26px;
}
.text-center {
  text-align: center;
}
.active::after {
  content: "";
  display: block;
  width: 20px;
  height: 3px;
  background: #fff;
  margin: -3px auto;
}
a {
  color: #fff;
}
</style>